body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #f9f9f9;
}

/* TodoListTemplat.js css 파일*/

.todo-list-template {
    backgrount: white;
    width: 512px;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0.23); /* 그림자 */
    margin: 0 auto; /* 페이지 중앙 정렬 */
    margin-top: 4rem;
}

.title{
    padding: 2rem;
    font-size: 2.5rem;
    text-align: center;
    font-weight: 100;
    background: #22b8cf;
    color: white;
}

.form-wrapper {
    padding: 1rem;
    border-bottom: 1px solid #22b8cf;
}

.todos-wrapper{
    min-height: 5rem;
}
/* src/component/Form.css */

.form{
    display: flex;
}

.form input {
    flex: 1 1; /* 버튼을 뺀 빈공간을 모두 채워 줍니다 */
    font-size: 1.25rem;
    outline: none;
    border: none;
    border-bottom: 1px solid #c5f6fa;
}

.create-button{
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 1rem;
    padding-right: 1rem;
    margin-left: 1rem;
    background: #22b8cf;
    border-radius: 3px;
    color: white;
    font-weight: 600;
    cursor: pointer;
}

.create-button:hover{
    background: #3bc9db
}
.todo-item{
    padding: 1rem;
    display: flex;
    align-itmes: center; /* 세로 가운데 정렬 */
    cursor: pointer;
    -webkit-transition: all o.15s;
    transition: all o.15s;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.todo-item:hover{
    background: #e3fafc;
}

/* todo-item 에 마우스가 있을때만 .remove 보이기 */
.todo-item:hover .remove{
    opacity: 1;
}

/* todo-item 사이에 윗 테두리 */
.todo-item + .todo-item{
    border-top: 1px solid #f1f3f5;
}

.remove{
    margin-right: 1rem;
    color: #e64980;
    font-weight: 600;
    opacity: 0;
}

.todo-text{
    flex: 1 1;/*  체크, 엑스를 제외한 공간 다 채우기 */
    word-break: break-all;
}

.checked{
    text-decoration: line-through;
    color: #adb5bd;
}
.check-mark{
    font-size: 1.5rem;
    line-height: 1rem;
    margin-left: 1rem;
    color: #3bc9db;
    font-weight: 800;
}
